import React, { useState } from 'react';
import './attend.css'
import { Link } from 'react-router-dom';

const Attend = () => {

    const [attending, setAttending] = useState(false);
    const [randomNumber, setRandomNumber] = useState(null);    

    const handleAttendanceClick = () => {
        if (!attending) {
        const random = Math.floor(100 + Math.random() * 900);
        setRandomNumber(random);
        setAttending(true);
        } else {
        setAttending(false);
        setRandomNumber(null);
        }
    };

    return (
            <div className='attendContainer'>
                
                <div className='leftContainer'>
                    {/*   ==================================================== */}
                    <span className='title'>   </span>
                    {/*   ~. */}
                    <div className='todayAttendTitle'>
                        <span>   </span>
                        <span style={{fontSize:'25px', color:'#2E5077', fontWeight:'800'}}> 
                            ( ) 
                        </span>
                        <span> . </span> <br/>
                    </div>

                    {/*    */}
                    <div className='todayAttendContent'>
                        <span style={{fontSize:'23px', color:'#2E5077', fontWeight:'700'}}>
                            (  - 25.05.19 )
                        </span>
                        <br />

                        {/* 3    */}
                        {randomNumber && (
                            <div style={{
                            fontSize: '60px',
                            fontWeight: 'bold',
                            color: '#2E5077',
                            }}>
                            {randomNumber}
                            </div>
                        )}

                        <button
                        onClick={handleAttendanceClick}
                        className={`attendButton ${attending ? 'end' : ''}`}
                        >
                        {attending ? ' ' : ' '}
                        </button>

                        <hr />
                        
                        {/*    */}
                        <div className='studentList'>
                            ( )
                            <hr style={{border:'1px solid #7D8A8A'}}/>
                            <span className='attenderTitle'> () </span>
                        </div>
                    </div>
                </div>


                <div className='rightContainer'>
                    {/*     =========================================== */}
                    <span className='title'>     </span>
                    <div className='stackAttend'>
                        {/*   */}
                        <div className='attendGraph'>
                             
                        </div>

                        {/*   % */}
                        <div className='attendClass'>
                            <span style={{fontSize:'25px', color:'#2E5077', fontWeight:'700'}}>     </span> &nbsp;        
                            <span style={{fontSize:'25px', color:'#79D7BE', fontWeight:'800'}}> ()% </span> &nbsp;                               
                            <span style={{fontSize:'25px', color:'#2E5077', fontWeight:'700'}}> ! </span>
                            
                            <div className="attenderWrapper">
                                <div className='attender'>
                                    <span className='attenderTitle'>    </span> <br />
                                    <span className='attenderName'> () </span>
                                </div>

                                <div className='attender'>
                                    <span className='attenderTitle'>    </span> <br />
                                    <span className='attenderName'> () </span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/*   ==================================================== */}
                    <span className='title'>   </span>
                    <div className='historyAttend'>
                        {/*    */}
                        <div className='historyList'>
                            <span style={{fontSize:'23px', color:'#2E5077', fontWeight:'700', display:'inline-block', marginRight:'20px'}}>
                                (  25.05.09  )
                            </span>

                            <span style={{display:'inline-block', marginRight:'10px'}}>
                                <i class="bi bi-circle-fill" style={{color:'#79D7BE'}}></i> {/*   */} 
                                <span className='historyCount'> () </span>
                            </span>
                            <span style={{display:'inline-block', marginRight:'10px'}}>
                                <i class="bi bi-triangle-fill" style={{color:'#FFB83C'}}></i> {/*   */}
                                <span className='historyCount'> () </span>
                            </span>
                            <span style={{display:'inline-block', marginRight:'500px'}}>
                                <i class="bi bi-x-lg" style={{color:'#D85858'}}></i> {/* X  */}
                                <span className='historyCount'> () </span>
                            </span>

                            {/* >    (&    ) */}
                            <Link to="/attend-history">
                                <i className="bi bi-chevron-right"></i>
                            </Link>
                        </div>
                    </div>
                </div>


                
            </div>
    );
};

export default Attend;