import React, { useState } from 'react';
import bgImg from "../image/background.png";
import logo from "../image/SquareLogo.png";
import "./Memberstyle.css";
import DaumPostcodeEmbed from 'react-daum-postcode';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const JoinPage = () => {
  const [openPostCode, setOpenPostCode] = useState(false);
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [pwdChk, setPwdChk] = useState('');

  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');

  const [address, setAddress] = useState('');
  const [aca_name, setAca_name] = useState('');
  const [description, setDescription] = useState('');

  const [warning, setWarning] = useState(false);
  const [idCheck, setIdCheck] = useState(false); // true: 
  const [pwdCheck, setPwdCheck] = useState(false);

  const navi = useNavigate();

  //  
  const onJoinSubmit = (e) => {
    e.preventDefault();
    if(!idCheck) {
      alert("   .");
      return;
    }

    if(!pwdCheck) {
      alert("  .");
      return;
    }

    if(address === '') {
      alert("  ");
      return;
    }
    
    axios.post("/public/join",
      {username, password, name, phone, email, aca_name, address, description}
    ).then(res => {
      if(res.data === 'success') {
        navi("/login");
      }
    });
  }


  //   
  const onInvalidCheck = () => {
    setWarning(true);
    btnIdCheck();
    checkPWD();
  }

  //   
  const btnIdCheck = () => {
    if(username.trim() === '') {
      setIdCheck(false);
      return;
    }

    let url = "/public/idCheck?username="+username;
    axios.get(url).then(res => {
      if(res.data === 'fail') {
        alert(" ");
        setIdCheck(false);
      } else {
        // alert("!");
        setIdCheck(true);
      }
    });
  }

  //  
  const checkPWD = () => {
    if(password.trim() === '' || pwdChk.trim() === '') {
      return;
    }

    if(password === pwdChk) {
      setPwdCheck(true);
      return;
    }
  }

  // KaKao  
  const handle = {
    click:() => {
      setOpenPostCode(current => !current);
    },

    //   
    selectAddress: (data) => {
      // console.log(data);
      setAddress(`(${data.zonecode})${data.address}`);
      setOpenPostCode(false);
    }
  }

  return (
    <div className="backstyle" style={{backgroundImage: `url(${bgImg})`}}>
      <img alt="" src={logo} className="logo" />
      <p className='joinTitle'></p>
      <div className='box2'>
        <form onSubmit={onJoinSubmit}>
          <label>
             <br />
            <input type='text' placeholder=' '
             className='form-control' required
             value={name}
             onChange={(e) => setName(e.target.value)} />
          </label><br />
          <label>
             <br />
            <input type='text' placeholder=' '
             className='form-control' required
             value={username}
             onChange={(e) => setUsername(e.target.value)} />
          </label>
          <label>
             <br />
            <input type='password' placeholder=' '
             className='form-control' required
             value={password}
             onChange={(e) => setPassword(e.target.value)} />
            <input type='password' placeholder=' '
             className='form-control' required
             onChange={(e) => setPwdChk(e.target.value)} />
          </label><br />
          <label>
             <br />
            <input type='tel' placeholder='010-xxxx-xxxx'
             className='form-control' required
             value={phone}
             onChange={(e) => setPhone(e.target.value)} />
            <input type='email' placeholder='square@AAA.com'
             className='form-control' required
             value={email}
             onChange={(e) => setEmail(e.target.value)} />
          </label><br />
          <label>
             <br />
            <input type='text' placeholder=' '
             className='form-control' readOnly required
             value={address}
             onChange={(e)=>setAddress(e.target.value)}
             onClick={handle.click}
             data-bs-toggle="modal" data-bs-target="#AddressModal" />
            
            <input type='text' placeholder=''
             className='form-control' required
             value={aca_name}
             onChange={(e) => setAca_name(e.target.value)} />
            <textarea className='form-control'
             placeholder=' '
             value={description}
             onChange={(e) => setDescription(e.target.value)}></textarea>
          </label>
          <div className='check'>
            {
              warning && 
              <>
              {(!idCheck||!pwdCheck) && <span className='failed'>  .</span>}
              {idCheck && pwdCheck && <span> .</span>}
              </>
            }
            <button type='button' className='btn btn-sm btn-danger'
             onClick={onInvalidCheck}></button>
          </div>
          <button type="submit" className="loginbtn"></button>
        </form>
      </div>
      {/*   */}
      {
        openPostCode && (
         <div className='modal-overlay'>
          <div className='modal-content'>
            <i class="bi bi-x modal-close"
             onClick={() => setOpenPostCode(false)}></i><br />
            <DaumPostcodeEmbed onComplete={handle.selectAddress}
             autoClose={false}
             defaultQuery=' 94 20' />
          </div>
         </div> 
        )
      }
    </div>
  );
};

export default JoinPage;